.page_body {
  margin: 0;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.nav__list {
  text-align: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  font-family: Roboto, Arial, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: .5px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
}

.card__product-image {
  width: 160px;
  height: 134px;
  padding: 16px 3px 0;
  display: flex;
}

.card__product-name {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-rating {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.card__product-rating-review {
  color: #060b35;
  align-self: flex-end;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__product-price {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__product-price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__product-price-numbers {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__product-buy-link {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__product-buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: #34568b;
}
